import React, { useLayoutEffect, useState, useRef, useCallback } from "react";
import { Arrow } from "./Arrow";
import "./styles.scss";

type CarouselProps = {
  show: number;
  infiniteLoop?: boolean;
  children: Array<React.ReactNode>;
};

export const Layout: React.FC<CarouselProps> = ({
  show,
  infiniteLoop,
  children,
}) => {
  const [data, setData] = useState<Array<React.ReactNode>>(children);
  const ref = useRef<HTMLDivElement | null>(null);
  const translateValue = 100;

  useLayoutEffect(() => {
    const carousel = ref.current;
    if (!carousel || !infiniteLoop) {
      return;
    }

    const interval = setInterval(() => {
      carousel.style.justifyContent = "flex-start";
      carousel.style.transform = `translateX(${-translateValue}%)`;
      const transferPart = data.splice(show);
      setData([...transferPart, ...data]);
    }, 2500);
    return () => clearInterval(interval);
  }, [translateValue, data, setData, infiniteLoop, show]);

  const prev = useCallback(() => {
    const carousel = ref.current;
    if (!carousel) {
      return;
    }

    carousel.style.justifyContent = "flex-end";
    carousel.style.transform = `translateX(${translateValue}%)`;
    const transferPart = data.splice(-show);
    setData([...transferPart, ...data]);
  }, [translateValue, data, setData, show]);

  const next = useCallback(() => {
    const carousel = ref.current;
    if (!carousel) {
      return;
    }

    carousel.style.justifyContent = "flex-start";
    carousel.style.transform = `translateX(${-translateValue}%)`;
    const transferPart = data.splice(show);
    setData([...transferPart, ...data]);
  }, [translateValue, data, setData, show]);

  const handleTransitionEnd = useCallback(() => {
    const carousel = ref.current;
    if (!carousel) {
      return;
    }

    carousel.style.transition = "none";
    carousel.style.transform = "translateX(0)";
    setTimeout(() => {
      carousel.style.transition = "transform .2s";
    });
  }, []);

  return (
    <div className="wrap">
      <button onClick={prev} className="left-arrow">
        <Arrow />
      </button>

      <div className="limiter">
        <div
          ref={ref}
          className={`carousel show-${show}`}
          onTransitionEnd={handleTransitionEnd}
        >
          {data}
        </div>
      </div>

      <button onClick={next} className="right-arrow">
        <Arrow />
      </button>
    </div>
  );
};
