import { DataType } from "types";
import * as Icon from "./icons";
import { useEffect, useState } from "react";
import classNames from "classnames";
import styles from "../../styles.module.scss";
import getStarted from "./get-started-screen.svg";
import online from "./online-screen.svg";
import congrat from "./congrat-screen.svg";

type StepsType = {
  id: number;
  imageSrc: string;
} & DataType;

export const stepsData: Array<StepsType> = [
  {
    id: 0,
    imageSrc: getStarted,
    icon: <Icon.Devices />,
    title: "Create a profile",
    text: "Download the app for free, Sign up for an account and let us to get know you",
  },
  {
    id: 1,
    imageSrc: online,
    icon: <Icon.FactCheck />,
    title: "Apply for a loan",
    text: "Select the loan amount and term on the calculator that suits for you",
  },
  {
    id: 2,
    imageSrc: congrat,
    icon: <Icon.Card />,
    title: "Get money!",
    text: "If you qualify your cash will be disbursed within a minute. Just add your account number",
  },
];

export const Steps = () => {
  const [counter, setCounter] = useState<number>(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCounter(counter === stepsData.length - 1 ? 0 : counter + 1);
      return stepsData[counter];
    }, 4000);

    return () => clearInterval(interval);
  }, [counter, setCounter]);

  const toggleActive = (index: number) => {
    setCounter(stepsData[index].id);
  };

  return (
    <div className={styles.steps}>
      {stepsData.map(({ id, imageSrc, icon, title, text }, index) => (
        <div
          key={id}
          className={classNames(
            styles.step,
            counter === index && `${styles.active}`
          )}
          onClick={() => toggleActive(index)}
        >
          <div className={styles.overlay}>{icon}</div>
          <div>
            <h3>{title}</h3>
            <p className="mobile:text-[14px]">{text}</p>
          </div>
          <img src={imageSrc} alt={title} width="354px" height="708px" />
        </div>
      ))}
    </div>
  );
};
